import { Flex, Image } from 'antd';
import './index.css';
const Home= () =>{
    return(
        <div className="logo">
            <Image 
            width={500}
            height={500}
            src = 'data:image/png;base64,'
            preview = {false}
            />
            <Flex  gap='large'>
                <span className='welcome'>Xidian NER</span>
                <span className='use-way'></span>
            </Flex>
        </div>
    )
}

export default Home